.wpte-product-container {

    [class^='wpte-product-cube-'],
    [class*=' wpte-product-cube-'],
    [class^='wpte-product-cube-'] .wpte-product-hover-figure,
    [class*=' wpte-product-cube-'] .wpte-product-hover-figure {
        background: transparent !important;
    }

    [class^='wpte-product-cube-'],
    [class*=' wpte-product-cube-'],
    * [class^='wpte-product-cube-'],
    * [class*=' wpte-product-cube-'] {
        perspective: 50em;
        transform-style: preserve-3d;
        overflow: visible !important;
    }

    [class^='wpte-product-cube-'] .wpte-product-hover-image,
    [class*=' wpte-product-cube-'] .wpte-product-hover-image {
        transition-delay: 0.05s;
    }

    [class^='wpte-product-cube-'] .wpte-product-hover-figure-caption,
    [class*=' wpte-product-cube-'] .wpte-product-hover-figure-caption {
        opacity: 0;
        transition-delay: 0s;
    }

    [class^='wpte-product-cube-']:hover .wpte-product-hover-image,
    [class*=' wpte-product-cube-']:hover .wpte-product-hover-image,
    [class^='wpte-product-cube-'].oxi-touch .wpte-product-hover-image,
    [class*=' wpte-product-cube-'].oxi-touch .wpte-product-hover-image {
        opacity: 0;
        transition-delay: 0s;
    }

    [class^='wpte-product-cube-']:hover .wpte-product-hover-figure-caption,
    [class*=' wpte-product-cube-']:hover .wpte-product-hover-figure-caption,
    [class^='wpte-product-cube-'].oxi-touch .wpte-product-hover-figure-caption,
    [class*=' wpte-product-cube-'].oxi-touch .wpte-product-hover-figure-caption {
        transform: translateY(0%) rotateX(0deg);
        opacity: 1;
        transition-delay: 0.05s;
    }

    .wpte-product-cube-up {
        .wpte-product-hover-figure-caption {
            transform: translateY(50%) rotateX(-90deg);
        }
        &:hover {
            .wpte-product-hover-image{
                transform: translateY(-50%) rotateX(90deg);
            }
        }
    }

    .wpte-product-cube-down {
        .wpte-product-hover-figure-caption {
            transform: translateY(-50%) rotateX(90deg);
        }
        &:hover {
            .wpte-product-hover-image{
                transform: translateY(50%) rotateX(-90deg);
            }
        }
    }

    .wpte-product-cube-left {
        .wpte-product-hover-figure-caption {
            transform: translateX(-50%) rotateY(-90deg);
        }
        &:hover {
            .wpte-product-hover-image{
                transform: translateX(50%) rotateY(90deg);
            }
        }
    }

    .wpte-product-cube-right {
        .wpte-product-hover-figure-caption {
            transform: translateX(50%) rotateY(90deg);
        }
        &:hover {
            .wpte-product-hover-image{
                transform: translateX(-50%) rotateY(-90deg);
            }
        }
    }
}